//import scss file
@import "./gradient-text.scss";
@import "./typography.scss";
@import "./button.scss";

//You can write css or scss
body {
  display: flex;
  align-items: center;
  height: 100vh;
  width: 100vw;
  flex-direction: column;
  padding: 1rem;
  // font-family: "Poppins", sans-serif;
  font-family: 'Gothic A1', sans-serif;
  line-height: 1.5;
  background-color: white;
  color: black;
  text-align: center;
}

.header {
  width: 96%;
  // background-color: red;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto;

  .logo {
    display: flex;

    h1 {
      font-family: 'Gothic A1', sans-serif;
      font-weight: 500;
    }
  }

  .contact {
    display: flex;
  }

}

@media only screen and (max-width: 600px) {
  .header {
    width: 96%;
  }
}

.pop-ups {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  #one {
    position: relative;
    padding: 24px;
    background-color: #33658A;
    transform: rotate(-8deg);
    color: white;
    border-radius: 16px;
    text-align: left;
  }

  #second {
    position: relative;
    padding: 24px;
    background-color: #FFB30F;
    transform: rotate(10deg);
    border-radius: 16px;
    text-align: left;
  }

  #third {
    position: relative;
    padding: 24px;
    background-color: #FD151B;
    transform: rotate(-5deg);
    border-radius: 16px;
    text-align: left;
  }
}


.button-list {
  width: 96%;
  // background-color: red;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 8px;
  margin: auto;
}


main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;

  img {
    width: 300px;
  }
}




.background-image {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 600px) {
  main {
    font-size: 10vw;
  }
}